import React, { useEffect, useRef, useState } from 'react';
import { Box, Typography, Container, Grid, Card, CardContent, CardMedia, List, ListItem, ListItemIcon, ListItemText, Modal, IconButton } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';


const darkTheme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#ffffff',
    },
    secondary: {
      main: '#747bff',
    },
    background: {
      default: '#000000',
      paper: 'rgba(255, 255, 255, 0.05)',
    },
    text: {
      primary: '#ffffff',
      secondary: 'rgba(255, 255, 255, 0.7)',
    },
  },
  typography: {
    fontFamily: '"Unbounded", sans-serif',
    h1: {
      fontSize: '2.5rem',
      fontWeight: 700,
    },
    h2: {
      fontSize: '2.2rem',
      fontWeight: 600,
    },
    h4: {
      fontSize: '1.8rem',
      fontWeight: 600,
      marginBottom: '1rem',
    },
    h6: {
      fontSize: '1.2rem',
      fontWeight: 600,
    },
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          backgroundColor: 'rgba(255, 255, 255, 0.05)',
          borderRadius: '10px',
          border: '1px solid rgba(255, 255, 255, 0.1)',
          transition: 'all 0.3s ease',
          '&:hover': {
            transform: 'translateY(-5px)',
            backgroundColor: 'rgba(255, 255, 255, 0.08)',
            boxShadow: '0 10px 20px rgba(0, 0, 0, 0.3)',
            borderColor: 'rgba(255, 255, 255, 0.2)',
          },
        },
      },
    },
  },
});

const Bones = () => {
  const skeletonModelRef = useRef(null);
  const [openImageModal, setOpenImageModal] = useState(false);
  const [currentImage, setCurrentImage] = useState('');
  const [imageScale, setImageScale] = useState(1);
  const [imagePosition, setImagePosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });

  const handleOpenImage = (imageSrc) => {
    setCurrentImage(imageSrc);
    setOpenImageModal(true);
    setImageScale(1); //      
    setImagePosition({ x: 0, y: 0 }); //  
  };

  const handleCloseImage = () => {
    setOpenImageModal(false);
  };

  const handleZoomIn = () => {
    setImageScale(prev => Math.min(prev + 0.2, 3));
    //         
    setImagePosition({ x: 0, y: 0 });
  };

  const handleZoomOut = () => {
    setImageScale(prev => Math.max(prev - 0.2, 0.5));
    //         
    setImagePosition({ x: 0, y: 0 });
  };

  const handleMouseDown = (e) => {
    //       
    if (e.button === 0) {
      setIsDragging(true);
      setDragStart({ x: e.clientX, y: e.clientY });
      e.preventDefault();
    }
  };

  const handleMouseMove = (e) => {
    if (isDragging) {
      //     
      const deltaX = e.clientX - dragStart.x;
      const deltaY = e.clientY - dragStart.y;
      
      //   
      setImagePosition(prev => ({
        x: prev.x + deltaX,
        y: prev.y + deltaY
      }));
      
      //      
      setDragStart({ x: e.clientX, y: e.clientY });
    }
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  //         
  useEffect(() => {
    if (openImageModal) {
      document.addEventListener('mouseup', handleMouseUp);
      document.addEventListener('mousemove', handleMouseMove);
    }
    
    return () => {
      document.removeEventListener('mouseup', handleMouseUp);
      document.removeEventListener('mousemove', handleMouseMove);
    };
  }, [openImageModal, isDragging, dragStart]);

  useEffect(() => {
    if (skeletonModelRef.current) {
      
      const scene = new THREE.Scene();
      scene.background = new THREE.Color(0x000000);

      let skullModel = null;

      const camera = new THREE.PerspectiveCamera(75, skeletonModelRef.current.clientWidth / skeletonModelRef.current.clientHeight, 0.1, 1000);
      camera.position.z = 5;

      const renderer = new THREE.WebGLRenderer({ antialias: true });
      renderer.setSize(skeletonModelRef.current.clientWidth, skeletonModelRef.current.clientHeight);
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.shadowMap.enabled = true;
      renderer.shadowMap.type = THREE.PCFSoftShadowMap;
      skeletonModelRef.current.appendChild(renderer.domElement);

      const ambientLight = new THREE.AmbientLight(0xffffff, 0.4);
      scene.add(ambientLight);

      const directionalLight = new THREE.DirectionalLight(0xffffff, 0.7);
      directionalLight.position.set(5, 5, 5);
      directionalLight.castShadow = true;
      directionalLight.shadow.mapSize.width = 1024;
      directionalLight.shadow.mapSize.height = 1024;
      scene.add(directionalLight);
      
      //       
      const frontLight = new THREE.DirectionalLight(0xffffff, 0.5);
      frontLight.position.set(0, 0, 5);
      scene.add(frontLight);
      
      const backLight = new THREE.DirectionalLight(0xffffff, 0.3);
      backLight.position.set(0, 0, -5);
      scene.add(backLight);

      //     
      const geometry = new THREE.BoxGeometry(2, 2, 2);
      const material = new THREE.MeshStandardMaterial({ 
        color: 0xeeeeee,
        roughness: 0.65,
        metalness: 0.2,
        emissive: 0x000000,
        emissiveIntensity: 0
      });
      const cube = new THREE.Mesh(geometry, material);
      scene.add(cube);

      //     
      const controls = new OrbitControls(camera, renderer.domElement);
      controls.enableDamping = true;
      controls.dampingFactor = 0.05;

      //    
      try {
        const loader = new GLTFLoader();
        loader.load('/models/skull.glb', (gltf) => {
          const model = gltf.scene;
          model.scale.set(2, 2, 2);
          scene.add(model);
          scene.remove(cube); //     

          //  
          const box = new THREE.Box3().setFromObject(model);
          const center = box.getCenter(new THREE.Vector3());
          model.position.x = -center.x;
          model.position.y = -center.y;
          model.position.z = -center.z;
          
          //    
          model.rotation.x = 0.1;
          model.rotation.y = -0.3;
          
          //      
          skullModel = model;

          //     
          model.traverse((child) => {
            if (child.isMesh) {
              child.material.color = new THREE.Color(0xeeeeee);
              child.material.emissive = new THREE.Color(0x000000);
              child.material.emissiveIntensity = 0;
              child.material.metalness = 0.2;
              child.material.roughness = 0.65;
            }
          });
        }, undefined, (error) => {
          console.error('  :', error);
          //   ,    
        });
      } catch (error) {
        console.error('    :', error);
        //       
      }

      // 
      const animate = () => {
        requestAnimationFrame(animate);
        
        //    
        if (cube) {
          cube.rotation.x += 0.01;
          cube.rotation.y += 0.01;
        }
        
        //    
        if (skullModel) {
          skullModel.rotation.y += 0.002;
        }
        
        controls.update();
        renderer.render(scene, camera);
      };
      animate();

      //    
      const handleResize = () => {
        if (skeletonModelRef.current) {
          camera.aspect = skeletonModelRef.current.clientWidth / skeletonModelRef.current.clientHeight;
          camera.updateProjectionMatrix();
          renderer.setSize(skeletonModelRef.current.clientWidth, skeletonModelRef.current.clientHeight);
          renderer.setPixelRatio(window.devicePixelRatio);
        }
      };
      window.addEventListener('resize', handleResize);

      //    
      return () => {
        window.removeEventListener('resize', handleResize);
        if (skeletonModelRef.current && renderer.domElement) {
          skeletonModelRef.current.removeChild(renderer.domElement);
        }
      };
    }
  }, []);

  return (
    <ThemeProvider theme={darkTheme}>
      <Container maxWidth="lg" sx={{ py: 4, mt: 2, mb: 4, px: { xs: 2, sm: 3, md: 4 } }}>
        <Typography variant="h2" component="h1" gutterBottom align="center" sx={{ mb: 4 }}>
            
        </Typography>
        
        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
             3D- 
          </Typography>
          <Box 
            ref={skeletonModelRef}
            sx={{ 
              width: '100%', 
              height: 500, 
              bgcolor: 'background.default',
              border: '1px solid rgba(255, 255, 255, 0.1)',
              borderRadius: 2,
              overflow: 'hidden'
            }}
          />
          <Typography variant="body2" sx={{ mt: 1, textAlign: 'center', color: 'text.secondary' }}>
                .     /.
          </Typography>
        </Box>
        
        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
              
          </Typography>
          <Typography variant="body1" paragraph>
                 ,    ,          .    22 : 8     ()  14     ().
          </Typography>
          <Typography variant="body1" paragraph>
                    ,          (- ).
          </Typography>
        </Box>
        
        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
              ?
          </Typography>
          <Typography variant="body1" paragraph>
                ,    .       ,      ,  ,     ,     .
          </Typography>
          <Typography variant="body1" paragraph>
                 206 ,     .       ,    .
          </Typography>
        </Box>
        
        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
              
          </Typography>
          <Grid container spacing={4}>
            <Grid item xs={12} md={6}>
              <Card>
                <Box
                  sx={{ 
                    height: 300, 
                    bgcolor: 'rgba(255, 255, 255, 0.03)',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    overflow: 'hidden',
                    border: '1px dashed rgba(255, 255, 255, 0.2)',
                    cursor: 'pointer'
                  }}
                  onClick={() => handleOpenImage('/images/systems/vertebra.png')}
                >
                  <img 
                    src="/images/systems/vertebra.png" 
                    alt=" " 
                    style={{ maxWidth: '100%', maxHeight: '100%', objectFit: 'cover' }}
                    onError={(e) => {
                      e.target.onerror = null;
                      e.target.style.display = 'none';
                      e.target.parentElement.innerHTML = '<Typography variant="body1" color="text.secondary"> </Typography>';
                    }}
                  />
                </Box>
                <CardContent>
                  <Typography variant="h6"> </Typography>
                  <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                          206 ,      .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={6}>
              <Card>
                <Box
                  sx={{ 
                    height: 300, 
                    bgcolor: 'rgba(255, 255, 255, 0.03)',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    overflow: 'hidden',
                    cursor: 'pointer'
                  }}
                  onClick={() => handleOpenImage('/images/bones/stroenie-kostei-cheloveka.jpg')}
                >
                  <img 
                    src="/images/bones/stroenie-kostei-cheloveka.jpg" 
                    alt="  " 
                    style={{ maxWidth: '100%', maxHeight: '100%', objectFit: 'cover' }} 
                  />
                </Box>
                <CardContent>
                  <Typography variant="h6"> </Typography>
                  <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                          ,      .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>
        
        <Grid container spacing={4} sx={{ mb: 6 }}>
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Typography variant="h5" component="h3" gutterBottom>
                    
                </Typography>
                <List>
                  <ListItem>
                    <ListItemIcon></ListItemIcon>
                    <ListItemText primary="     " />
                  </ListItem>
                  <ListItem>
                    <ListItemIcon></ListItemIcon>
                    <ListItemText primary="    " />
                  </ListItem>
                  <ListItem>
                    <ListItemIcon></ListItemIcon>
                    <ListItemText primary="       " />
                  </ListItem>
                  <ListItem>
                    <ListItemIcon></ListItemIcon>
                    <ListItemText primary="    ,    " />
                  </ListItem>
                  <ListItem>
                    <ListItemIcon></ListItemIcon>
                    <ListItemText primary="      " />
                  </ListItem>
                </List>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Typography variant="h5" component="h3" gutterBottom>
                   
                </Typography>
                <Typography variant="body1" paragraph>
                         ,     .
                </Typography>
                <Typography variant="body1" paragraph>
                       ,   .    ,   .
                </Typography>
                <Typography variant="body1">
                        ()    (, ).
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
             
          </Typography>
          <Typography variant="body1" paragraph>
                    :
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6} md={3}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2">
                    , , ,  .  ,   .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2">
                    ,  , , .   .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2">
                    , ,  .   .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2">
                    ,  , .     .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
              
          </Typography>
          <Typography variant="body1" paragraph>
                  :
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <Card sx={{ height: '100%', overflow: 'visible' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                      
                  </Typography>
                  <Typography variant="body2">
                          . :
                  </Typography>
                  <Box component="ul" sx={{ pl: 2, mt: 1 }}>
                    <Box component="li" sx={{ mb: 0.5 }}> </Box>
                    <Box component="li" sx={{ mb: 0.5 }}>  (2)</Box>
                    <Box component="li" sx={{ mb: 0.5 }}>  (2)</Box>
                    <Box component="li" sx={{ mb: 0.5 }}> </Box>
                    <Box component="li" sx={{ mb: 0.5 }}> </Box>
                    <Box component="li" sx={{ mb: 0.5 }}> </Box>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Card sx={{ height: '100%', overflow: 'visible' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                      
                  </Typography>
                  <Typography variant="body2">
                          . :
                  </Typography>
                  <Box component="ul" sx={{ pl: 2, mt: 1 }}>
                    <Box component="li" sx={{ mb: 0.5 }}>  (2)</Box>
                    <Box component="li" sx={{ mb: 0.5 }}> </Box>
                    <Box component="li" sx={{ mb: 0.5 }}>  (2)</Box>
                    <Box component="li" sx={{ mb: 0.5 }}>  (2)</Box>
                    <Box component="li" sx={{ mb: 0.5 }}>  (2)</Box>
                    <Box component="li" sx={{ mb: 0.5 }}>   </Box>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
                
          </Typography>
          <Typography variant="body1" paragraph>
                  ,   :
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Card>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                     
                  </Typography>
                  <List>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText 
                        primary="" 
                        secondary="    . :  , , , ." 
                      />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText 
                        primary="" 
                        secondary="   . : , , , ." 
                      />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText 
                        primary="" 
                        secondary="   . : , ,  ." 
                      />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText 
                        primary=" D" 
                        secondary="   . :  ,  ,   ." 
                      />
                    </ListItem>
                  </List>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={6}>
              <Card>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                      
                  </Typography>
                  <List>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText primary="       ,  " />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText primary="     ,   " />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText primary="       " />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText primary="    ,     " />
                    </ListItem>
                    <ListItem>
                      <ListItemIcon></ListItemIcon>
                      <ListItemText primary="  ,   K (  , , ),      " />
                    </ListItem>
                  </List>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" gutterBottom>
                
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2" paragraph>
                        ,       .
                  </Typography>
                  <Typography variant="subtitle1" gutterBottom>
                    :
                  </Typography>
                  <Typography variant="body2">
                          D<br/>
                       <br/>
                           
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2" paragraph>
                    ,    D,        .
                  </Typography>
                  <Typography variant="subtitle1" gutterBottom>
                    :
                  </Typography>
                  <Typography variant="body2">
                        <br/>
                       D<br/>
                      
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="h6" component="h3" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body2" paragraph>
                      ,         .
                  </Typography>
                  <Typography variant="subtitle1" gutterBottom>
                    :
                  </Typography>
                  <Typography variant="body2">
                      <br/>
                        <br/>
                         
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        <Box>
          <Typography variant="h4" component="h2" gutterBottom>
                
          </Typography>
          <Typography variant="body1" paragraph>
                    .           .
          </Typography>
          <Typography variant="h6" gutterBottom>
               :
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="subtitle1" gutterBottom>
                     
                  </Typography>
                  <Typography variant="body2">
                      ,    ,         .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="subtitle1" gutterBottom>
                       
                  </Typography>
                  <Typography variant="body2">
                    , , ,   ,      .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <Card sx={{ height: '100%' }}>
                <CardContent>
                  <Typography variant="subtitle1" gutterBottom>
                      
                  </Typography>
                  <Typography variant="body2">
                    -, ,        ,     .
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        {/*      */}
        <Modal
          open={openImageModal}
          onClose={handleCloseImage}
          aria-labelledby="image-modal-title"
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            bgcolor: 'rgba(0, 0, 0, 0.9)'
          }}
        >
          <Box 
            sx={{ 
              position: 'relative',
              width: '100%',
              height: '100%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              outline: 'none',
              p: 2,
              cursor: isDragging ? 'grabbing' : 'default'
            }}
            onClick={handleCloseImage}
          >
            <Box 
              onClick={(e) => e.stopPropagation()}
              sx={{
                position: 'relative',
                maxWidth: '90%',
                maxHeight: '90%',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
              }}
            >
              <Box
                sx={{
                  overflow: 'hidden',
                  cursor: isDragging ? 'grabbing' : 'grab',
                  maxWidth: '100%', 
                  maxHeight: '80vh',
                }}
              >
                <img 
                  src={currentImage} 
                  alt=" " 
                  style={{ 
                    maxWidth: '100%', 
                    maxHeight: '80vh',
                    transform: `scale(${imageScale}) translate(${imagePosition.x}px, ${imagePosition.y}px)`,
                    transition: isDragging ? 'none' : 'transform 0.2s ease',
                    objectFit: 'contain',
                    userSelect: 'none',
                    pointerEvents: 'none'
                  }} 
                  onMouseDown={handleMouseDown}
                  draggable="false"
                />
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  gap: 2,
                  mt: 2,
                  bgcolor: 'rgba(0, 0, 0, 0.7)',
                  borderRadius: 2,
                  p: 1
                }}
              >
                <IconButton 
                  onClick={handleZoomOut} 
                  sx={{ color: 'white', bgcolor: 'rgba(255,255,255,0.1)', '&:hover': { bgcolor: 'rgba(255,255,255,0.2)' } }}
                >
                  <Typography variant="h5"></Typography>
                </IconButton>
                <IconButton 
                  onClick={handleZoomIn} 
                  sx={{ color: 'white', bgcolor: 'rgba(255,255,255,0.1)', '&:hover': { bgcolor: 'rgba(255,255,255,0.2)' } }}
                >
                  <Typography variant="h5">+</Typography>
                </IconButton>
                <IconButton 
                  onClick={() => setImagePosition({ x: 0, y: 0 })}
                  sx={{ color: 'white', bgcolor: 'rgba(255,255,255,0.1)', '&:hover': { bgcolor: 'rgba(255,255,255,0.2)' } }}
                >
                  <Typography variant="body1"></Typography>
                </IconButton>
                <IconButton 
                  onClick={handleCloseImage} 
                  sx={{ color: 'white', bgcolor: 'rgba(255,255,255,0.1)', '&:hover': { bgcolor: 'rgba(255,255,255,0.2)' } }}
                >
                  <Typography variant="body1"></Typography>
                </IconButton>
              </Box>
            </Box>
          </Box>
        </Modal>
      </Container>
    </ThemeProvider>
  );
};

export default Bones; 